/*
   PAGE LOADING CSS
   ---------------- */
.preload * {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -ms-transition: none !important;
  -o-transition: none !important; }

.loading-wrap {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #010860;
  background: rgba(1, 8, 96, 0.9);
  z-index: 2000; }

.loading-text {
  color: #6F71B9;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 12px;
  font-weight: bold;
  left: 0;
  margin: 20px 0 0 0;
  position: absolute;
  top: 50%;
  text-align: center;
  width: 100%; }

.loading-bars {
  height: 30px;
  left: 50%;
  margin: -30px 0 0 -20px;
  position: absolute;
  top: 50%;
  width: 40px; }

.loading-bars .bar {
  background: #01CA60;
  bottom: 1px;
  height: 3px;
  position: absolute;
  width: 3px;
  -webkit-animation: sound 0ms -800ms linear infinite alternate;
  animation: sound 0ms -800ms linear infinite alternate; }

@-webkit-keyframes sound {
  0% {
    opacity: .35;
    height: 3px; }
  100% {
    opacity: 1;
    height: 28px; } }

@keyframes sound {
  0% {
    opacity: .35;
    height: 3px; }
  100% {
    opacity: 1;
    height: 28px; } }

.bar:nth-child(1) {
  left: 1px;
  -webkit-animation-duration: 406ms;
  animation-duration: 443ms; }

.bar:nth-child(2) {
  left: 5px;
  -webkit-animation-duration: 434ms;
  animation-duration: 498ms; }

.bar:nth-child(3) {
  left: 9px;
  -webkit-animation-duration: 415ms;
  animation-duration: 418ms; }

.bar:nth-child(4) {
  left: 13px;
  -webkit-animation-duration: 498ms;
  animation-duration: 415ms; }

.bar:nth-child(5) {
  left: 17px;
  -webkit-animation-duration: 437ms;
  animation-duration: 413ms; }

.bar:nth-child(6) {
  left: 21px;
  -webkit-animation-duration: 448ms;
  animation-duration: 455ms; }

.bar:nth-child(7) {
  left: 25px;
  -webkit-animation-duration: 444ms;
  animation-duration: 438ms; }

.bar:nth-child(8) {
  left: 29px;
  -webkit-animation-duration: 493ms;
  animation-duration: 479ms; }

.bar:nth-child(9) {
  left: 33px;
  -webkit-animation-duration: 491ms;
  animation-duration: 420ms; }

.bar:nth-child(10) {
  left: 37px;
  -webkit-animation-duration: 471ms;
  animation-duration: 412ms; }

